
html {
  width: 100%;
  height: 100%;
}
body {

  font: 14px/1.5 Helvetica, Arial, "Microsoft Yahei", "Hiragino Sans GB", "Heiti SC", "WenQuanYi Micro Hei", sans-serif;
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
ul,li {
  list-style: none;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden
}
.clearfix {
  *+height: 1%;
}
a {
  text-decoration: none;
  color: #333;
}
#main-page {
  width: 100%;
  padding-top: 60px;
  background: #f5f5f5;
}



